import React from "react";
import { useGLTF } from "@react-three/drei";

export const Model = React.memo((props) => {
  const { nodes, materials } = useGLTF(`./assets/models/${props.model}`);

  return (
    <group {...props} dispose={null}>
      <group dispose={null}>
        <mesh geometry={nodes["w1ll-k"].geometry} material={materials.k}>
          <meshStandardMaterial color={props.color} />
        </mesh>
        <mesh geometry={nodes["w1ll-b"].geometry} material={materials.b}>
          <meshStandardMaterial color={"#fff"} />
        </mesh>
      </group>
    </group>
  );
});
