import React from "react";

import CatalogItem from "../components/CatalogItem";
import { catalogData } from "../data/items";

const Catalog = () => {
  const [onAnimation, setOnAnimation] = React.useState(true);

  return (
    <section id="catalog">
      <div className="wrapper">
        <h1>КАТАЛОГ</h1>
        <span>Элементы можно поворачивать</span>
        <div className="catalog">
          {catalogData.map((item, i) => (
            <CatalogItem
              onAnimation={onAnimation}
              setOnAnimation={setOnAnimation}
              key={i}
              item={item}
              index={i}
            />
          ))}
        </div>
      </div>
    </section>
  );
};

export default Catalog;
