import React from "react";
import { colors } from "../../data/colors";

const Palette = React.memo(({ position, setColor }) => {
  return (
    <div className="palette" style={{ left: position }}>
      {colors.map((color, i) => (
        <div
          key={i}
          className="color"
          style={{ background: color.hex }}
          onClick={() => {
            setColor(color);
          }}></div>
      ))}
    </div>
  );
});

export default Palette;
