import React from "react";
import { CartContext } from "../App";
import colorsImg from "../assets/images/colors.png";
import drawingImg from "../assets/images/drawing.png";
import model from "../assets/images/model.png";
import resetImg from "../assets/images/reset.png";
import { colors } from "../data/colors";
import Palette from "./UI/Palette";
import Select from "./UI/Select";
import CanvasObject from "./CanvasObject";

// const CanvasObject = React.lazy(import("./CanvasObject"));

const CatalogItem = ({ item, index, onAnimation, setOnAnimation }) => {
  const [palettePosition, setPalettePosition] = React.useState(5);
  const [visibleDrawing, setVisibleDrawing] = React.useState(false);
  const [currentColor, setCurrentColor] = React.useState(colors[0]);
  const [selectValue, setSelectValue] = React.useState("0.4mm");

  const [cartStatus, setCartStatus] = React.useState(false);

  const orbit = React.useRef();

  const { setCart } = React.useContext(CartContext);

  function addToCart(item) {
    setCartStatus(true);
    setCart((prev) => [...prev, item]);
    setTimeout(() => {
      setCartStatus(false);
    }, 2000);
  }
  return (
    <article className="catalog__item" key={index}>
      <div className="catalog__item-img">
        {visibleDrawing ? (
          <img src={`./assets/images/drawings/${item.drawing}`} alt="" />
        ) : (
          <CanvasObject item={item} currentColor={currentColor} ref={orbit} />
        )}
      </div>
      <p className="name">{item.name}</p>
      <div className="row">
        <p>2 м.п.</p>
        <Select value={selectValue} setValue={setSelectValue} />
        <p className="price">{item.prices[selectValue]} ₽</p>
      </div>
      {visibleDrawing ? (
        <div className="razmetka">Развёртка {item.razmetka}</div>
      ) : (
        <span className="color-name">{currentColor.name}</span>
      )}

      {/* <button
        onClick={() => {
          console.log(orbit.current.object);
          const pos = orbit.current.object.position;
          console.log([pos.x, pos.y, pos.z]);
          const rot = orbit.current.object.rotation;
          console.log([rot._x, rot._y, rot._z]);
        }}>
        123
      </button> */}

      <button
        className={cartStatus ? "disable" : undefined}
        onClick={() => {
          addToCart({ name: item.name, selected: selectValue, prices: item.prices, count: 1 });
        }}>
        {cartStatus ? "Добавлено" : "В корзину"}
      </button>
      <img
        src={colorsImg}
        alt=""
        className="change-color"
        onClick={() => {
          setPalettePosition((prev) => (prev === 5 ? -35 : 5));
        }}
      />
      <img
        src={visibleDrawing ? model : drawingImg}
        style={{ animation: onAnimation ? "drawing 2.5s ease-in infinite" : "none" }}
        alt=""
        className="drawing"
        onClick={() => {
          setVisibleDrawing((prev) => !prev);
          setOnAnimation(false);
        }}
      />
      <img src={resetImg} alt="" className="reset" onClick={() => orbit.current.reset()} />
      <Palette position={palettePosition} setColor={setCurrentColor} />
      <div className="bg"></div>
    </article>
  );
};

export default React.memo(CatalogItem);
